import './App.css';
import { useState } from "react";

const exDb = {
  Chest: [
    { name: "Dumbell Press"},
    { name: "Bench Press"},
    { name: "Push Ups"}
  ],
  Arms: [
    { name: "Dumbell Curls"},
    { name: "Preacher"},
    { name: "Single-Hand Dumbell"}
  ],
  Legs: [
    { name: "Lunges"},
    { name: "Squat"},
    { name: "Walk" }
  ],
  Core: [
    { name: "Sit Ups"},
    { name: "Side Raise"},
    { name: "Plank"},
    { name: "Crunches"}
  ]
};

export default function App() {
  const [selectedexercise, setexercise] = useState("Chest");
  function exerciseClickHandler(exercise) {
    setexercise(exercise);
  }
  return (
    <div className="App">
      <h1> ExerciseTrainer </h1>

      <div>
        {Object.keys(exDb).map((exercise) => (
          <button onClick={() => exerciseClickHandler(exercise)} className="button">
            {exercise}
          </button>
        ))}
      </div>
      <hr />
      <div className="items">
        <ul>
          {exDb[selectedexercise].map((exercise) => (
            <li
              key={exercise.name}
              style={{
                listStyle: "none",
                padding: "1rem",
                border: "1px solid #000000",
                margin: "1rem 0rem",
                borderRadius: "0.5rem",
                color: "#FFFFFF",
                backgroundColor: "#3b82f6"
              }}
            >
              <div style={{ fontSize: "larger" }}> {exercise.name} </div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
